body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.title{
  border: 7px dashed rgb(10, 10, 159);
  margin: 6vh;
  font-size: 3rem;
  border-radius: 20px;
  padding: 1vh 5vw;
}
.cards-container{
  
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    max-width: 1500px;
    margin: 0px auto;
}

.card{
  width: 400px;
  height: 700px;
  margin: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  align-items: center;
  box-shadow: rgba(0,0,0,0.24) 0px 3px 8px;
}
.image{
  width: 400px;
  aspect-ratio: 1/1;
  object-fit: cover;
}
/* .detl{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;
} */
.btn{
    margin-top: 18px;
    padding: 8px 80px;
    border-radius: 10px;
    border: 1px solid #b4161b;
    font-size: 18px;
    font-weight: bold;
    background-color: #b4161b21;
}
.btn:hover{
  background-color: red;
    color: white;
    transition: 0.2s all;
}
.info{
  /* padding: 15px; */
  color: blue;
  cursor: pointer;
}
.price{
  color:#1faa59;
  font-size: 1.3rem;
  font-weight: 500;
}
.name{
  font-size: 1.5rem;
  font-weight: 500;
}
.start{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0px;
}

.no-data{
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.refresh{
    padding: 8px 70px;
    border: none;
    margin-top: 10px;
    transition: 0.4s all;
}
.refresh:hover{
  background-color: white;
}